<template>
	<view class="shop container">
		<!-- 搜索框 -->
		<view class="searchBox height">
			<u-icon class="tab_more" label="更多" label-pos="bottom" label-size="24" margin-top="10" size="50" name="arrow-down" color="white" label-color="white" @click="more"></u-icon>
			<navigator url="/pages/find/find" class="serach" hover-class="none">
				<u-icon size="40" name="search"></u-icon>
				<span>搜索</span>
			</navigator>
			<navigator class="tab_fenlei" url="/pages/shopClassify/shopClassify" hover-class="none">
				<u-icon label="分类" label-pos="bottom" label-size="24" margin-top="10" size="50" name="grid-fill" color="white" label-color="white"></u-icon>
			</navigator>
		</view>
		<!-- 背景图 -->
		<!-- <view class="bg"></view> -->
		<!-- 限制内容的区域 让其在中部体现 -->
		<view class="content">
			<!-- 轮播图 -->
			<view class="swiper_box">
				<swiper class="swiper" :autoplay="true" :interval="3000" :duration="500" :indicator-dots="false" circular="true" @change="currentChange">
					<swiper-item :style="{ 'background-color': item.background }" class="swiperitem" v-for="(item, index) in banner" :key="index">
						<image :src="item.url" mode="scaleToFill"></image>
					</swiper-item>
				</swiper>
				<view class="current">{{ current }}/{{ banner.length }}</view>
			</view>
			<!-- 购物车 图标 -->
			<view class="shop_car_box">
				<text class="cart-count">{{cartGoodsCount || 0}}</text>
				<!-- <image @click="goshopCar" class="shop_car" src="/static/car.png" mode=""></image> -->
				<text @click="goshopCar" class="t-icon icongouwuche"></text>
			</view>
			<!-- 分类图标 -->
			<view class="m-menu jhx_bg0">
				<navigator hover-class="none" class="item" :url="'/pages/shop/category?id=' + item.id" v-for="item in channel" :key="item.id">
					<image :src="item.iconUrl" background-size="cover"></image>
					<text class="jhx_f2">{{ item.name }}</text>
				</navigator>
			<!-- 	<navigator hover-class="none" class="item" url="/pages/catalog/catalog" open-type="switchTab">
					<image src="/static/channel_category.png" background-size="cover"></image>
					<text class="jhx_f2">更多分类</text>
				</navigator> -->
			</view>

			<!-- 活动专场 -->
			<view class="a-section a-topic" v-if="topics.length > 0">
				<view class="h">
					<view>
						<navigator url="/pages/topic/topic" hover-class="none"><text class="txt jhx_f4">活动专场</text></navigator>
					</view>
				</view>
				<view class="b">
					<scroll-view scroll-x class="list">
						<view class="item" v-for="(item, index) in topics" :key="item.id">
							<navigator :url="'../topicDetail/topicDetail?id=' + item.id" hover-class="none">
								<image class="img" :src="item.picUrl" background-size="cover"></image>
								<view class="np">
									<text class="name jhx_f7">{{ item.title }}</text>
									<text class="price jhx_f8">￥{{ item.price }}元起</text>
								</view>
								<text class="desc jhx_f5">{{ item.subtitle }}</text>
							</navigator>
						</view>
					</scroll-view>
				</view>
			</view>

			<!-- 商品瀑布流的渲染 -->
			<shop-waterfall :list="newGoods"></shop-waterfall>

		</view>

		<!-- 展示更多的 弹出框 -->
		<uni-popup ref="popupShare" type="top"><uni-popup-more @changeCommentsNum="changeCommentsNum" :videoId="videoId" @select="select" @close="close"></uni-popup-more></uni-popup>
	</view>
</template>

<script>
// 引入展示更多的 弹出框
import uniPopupMore from '../../components/uni-popup/uni-popup-more.vue';
//测试用数据
import Json from '../../Json.js';
// 引入商品瀑布流
import shopWaterfall from '@/components/shop-waterfall.vue';
// import QrcodePoster from '/components/zhangyu-qrcode-poster.vue'
import MescrollItem from '@/components/mescroll-swiper-item.vue';
import MescrollMixin from '@/components/mescroll-uni/mescroll-mixins.js';
// 引入tabHead 切换
// import swiperTabHead from '@/components/swiper-tab-head.vue';
export default {
	data() {
		return {
			id: '',
			cartGoodsCount:'', //当前购物车内的商品
			goods: [], // 商品列表
			banner: [], //swiper轮播图图片
			// 轮播图的指示点
			current: 1,
		
			tabIndex: 0,
			swiper_height: '', // 需要固定swiper的高度
			height: 0,
			tabTop: 0,

			// 插件数据
			channel: [], //分类专区
			topics: [], //活动专场
			newGoods: [''] //新品首发
		};
	},
	components: {
		shopWaterfall,
		// swiperTabHead,
		uniPopupMore,
		MescrollItem
	},
	onLoad() {
		let _this = this;
		// 发起请求进行网络请求
		this.api._get('home/index', {}, function(res) {
			console.log('请求数据', res);

			// 配置 商城数据
			_this.banner = res.data.banner;
			_this.channel = res.data.channel;
			_this.topics = res.data.topicList;
			_this.newGoods = res.data.newGoodsList;
		});

		// this.$refs.popupShare.open();
		// 获取商品的 展示列表
		// this.loadData();
		// 设置滚动区域的高度
		uni.getSystemInfo({
			success(res) {
				// console.log('页面信息res',res)
				_this.phoneHeight = res.windowHeight; //获取用户设备的高度
				console.log('用户的设备高度', res.windowHeight);
				// 计算组件的高度
				let view = uni.createSelectorQuery().selectAll('.height');
				view
					.boundingClientRect(data => {
						// 计算上方各元素的高度总和
						data.forEach((item, index) => {
							// console.log('item',item.height)
							_this.height += parseInt(item.height);
						});
						_this.swiper_height = _this.phoneHeight - _this.height;
						// console.log('_this.height',_this.height)
						_this.swiper_height = _this.swiper_height + 'px';
					})
					.exec();
			}
		});

		// 获取搜索框的高度,进而决定，分类列表头部距离顶部的粘性定位
		let view = uni.createSelectorQuery().selectAll('.searchBox');
		view
			.boundingClientRect(data => {
				// 计算上方各元素的高度总和
				data.forEach((item, index) => {
					// console.log('item',item.height)
					_this.height += parseInt(item.height);
				});
				_this.tabTop = _this.height;
			})
			.exec();
	},
	onReady() {
		// 动态设置滚动区域的高度
		let _this = this;
		uni.getSystemInfo({
			success(res) {
				// console.log('页面信息res',res)
				_this.phoneHeight = res.windowHeight; //获取用户设备的高度
				// console.log(res.windowHeight);
				// 计算组件的高度
				let view = uni.createSelectorQuery().selectAll('.height');
				view
					.boundingClientRect(data => {
						// 计算上方各元素的高度总和
						data.forEach((item, index) => {
							console.log('item', item.height);
							_this.height += parseInt(item.height);
						});
						_this.height = _this.height + 'px';
					})
					.exec();
			}
		});
	},
	onShow() {
		let that = this
		// that.cartGoodsCount = 3
		// 获取当前 购物车之中 商品数量
		this.api._get('cart/goodscount',{
			id:that.id
		},function( res ){
			console.log('onShow res',res.data )
			that.cartGoodsCount = res.data 
		})
	},
	methods: {
		// 展示更多内容
		more() {
			console.log('展示更多');
			this.$refs.popupShare.open();
		},
		close() {
			this.$refs.popupShare.close();
		},
		// loadData() {
		// 	// 获取商品列表中的数据
		// 	this.goods = Json['goodsList'];
		// 	// 获取轮播图中的数据
		// 	// this.swiperImg = Json['carouselList']
		// },
		// 监控轮播图的变化
		currentChange(e) {
			// console.log('轮播图变化',e.detail.current)
			this.current = e.detail.current + 1;
		},
		// 跳转到购物车页面
		goshopCar() {
			uni.navigateTo({
				url: '/pages/shopcar/shopcar'
			});
		},
		// 控制tab切换
		//接受子组件传过来的值点击切换导航
		tabtap(index) {
			console.log('点击切换');
			this.tabIndex = index;
		},
		//滑动切换导航
		tabChange(e) {
			this.tabIndex = e.detail.current;
		}
	}
};
</script>

<style lang="less">
.shop {
	// 搜索框
	.searchBox {
		width: 100%;
		height: 99rpx;
		// background-color: #f1c91c;
		background-color: #85c43f;
		padding: 20rpx 0;
		position: sticky;
		z-index: 10;
		top: 0;
		left: 0;
		display: flex;
		align-items: center;
		box-sizing: border-box;
		padding: 10rpx 20rpx;
		margin-bottom: 5px;
		padding-bottom: 30rpx;
		.serach {
			background-color: white;
			border-radius: 33rpx;
			width: 100%;
			height: 100%;
			text-align: center;
			color: #453a74;
			display: flex;
			align-items: center;
			justify-content: center;
			image {
				width: 40rpx;
				height: 40rpx;
				margin-right: 13rpx;
			}
		}
		.tab_more {
			margin-right: 18rpx;
		}
		.tab_fenlei {
			margin-left: 18rpx;
		}
	}

	// 背景图
	.bg {
		width: 100%;
		height: 160px;
		background-color: #f83f20;
		position: absolute;
		top: 0;
		left: 0;
		border-radius: 0 0 20rpx 20rpx;
	}

	// 内容区域
	.content {
		box-sizing: border-box;
		padding: 0 20rpx;
		overflow: hidden;
		// 轮播图 盒子
		.swiper_box {
			position: relative;
			border-radius: 25rpx;
			overflow: hidden;
			.swiper {
				.swiperitem {
					image {
						width: 100%;
						height: 100%;
					}
				}
			}
			.current {
				width: 56rpx;
				height: 44rpx;
				font-size: 27rpx;
				position: absolute;
				bottom: 10px;
				right: 11px;
				background-color: #f83f20;
				color: white;
				border-radius: 20rpx;
				box-sizing: border-box;
				padding: 7rpx 10rpx;
				opacity: 0.8;
			}
		}
		// 购物车图标
		.shop_car_box {
				width: 73rpx;
				height: 73rpx;
				// background-color: #f83f20;
				border-radius: 50%;
				overflow: hidden;
				position: fixed;
				bottom: 33rpx;
				right: 40rpx;
				z-index: 20;
				opacity: 1;
				.shop_car {
					width: 100%;
					height: 100%;
				}
				.cart-count{
					height: 28rpx;
					width: 28rpx;
					z-index: 10;
					position: absolute;
					top: 6rpx;
					right:  8rpx;
					background: #b4282d;
					text-align: center;
					font-size: 18rpx;
					color: #fff;
					line-height: 28rpx;
					border-radius: 50%;
						
				}
			}
		
		
		
	
		// 分类图标
		.cate-section {
			display: flex;
			justify-content: space-around;
			align-items: center;
			flex-wrap: wrap;
			padding: 30upx 22upx;
			background: #fff;
			.cate-item {
				display: flex;
				flex-direction: column;
				align-items: center;
				font-size: 26upx;
				color: #303133;
			}
			image {
				width: 88upx;
				height: 88upx;
				margin-bottom: 14upx;
				border-radius: 50%;
				opacity: 0.7;
				box-shadow: 4upx 4upx 20upx rgba(250, 67, 106, 0.3);
			}
		}
	}
}

// 插件样式
.m-menu {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	padding-bottom: 0rpx;
	padding-top: 30rpx;
}

.m-menu .item {
	width: 139rpx;
	height: 136rpx;
}

.m-menu image {
	display: block;
	width: 64rpx;
	height: 64rpx;
	margin: 0 auto;
	margin-bottom: 16rpx;
}

.m-menu text {
	display: block;
	text-align: center;
	margin: 0 auto;
	line-height: 1;
	font-size: 24rpx;
}

.a-section {
	// width: 750rpx;
	width: 100%;
	height: auto;
	overflow: hidden;
	background: #fff;
}

.a-section .h {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: center;
	height: 130rpx;
}

.a-section .h .txt {
	padding-right: 30rpx;
	background-size: 16.656rpx 27rpx;
	display: inline-block;
	height: 36rpx;
	line-height: 36rpx;
}
.container .a-section .title {
	height: 120rpx;
	border-top: 0rpx solid #eee;
	padding-top: 14rpx;
	margin: 0 30rpx;
}

.container .a-section .title .text {
	width: 530rpx;
}

.container .a-section .title .text .name {
	margin-bottom: 5rpx;
	position: relative;
}

.container .a-section .title .text .name .new {
	position: absolute;
	top: 6rpx;
	left: 210rpx;
	font-size: 20rpx;
	font-weight: bold;
}

.container .a-section .title .more .iconfont {
	margin-left: 9rpx;
	font-size: 26rpx;
	vertical-align: 3rpx;
}

.container .a-section .hot-bg {
	width: 100%;
	height: 215rpx;
	background-image: url('data:image/png;base64,');
	background-repeat: no-repeat;
	background-size: 100% 100%;
	box-sizing: border-box;
	padding: 14rpx;
	margin-bottom: 0;
}

.container .a-section .hot-bg .title {
	height: 87rpx;
}

.container .a-section .hot-bg .title .text {
	width: 575rpx;
	font-size: 24rpx;
	color: #fff;
}

.container .a-section .hot-bg .title .text .label {
	font-size: 30rpx;
	font-weight: bold;
	margin-right: 20rpx;
	color: #fff;
}

.container .a-section .hot-bg .title .more {
	font-size: 26rpx;
	color: #fff;
}

.container .a-section .hot-bg .title .more .iconfont {
	font-size: 25rpx;
	vertical-align: 2rpx;
	margin-left: 10rpx;
	color: #fff;
}
.a-topic .b {
	height: 533rpx;
	width: 100%;
	padding: 0 0 48rpx 0;
}

.a-topic .b .list {
	height: 533rpx;
	width: 100%;
	white-space: nowrap;
}

.a-topic .b .item {
	display: inline-block;
	height: 533rpx;
	width: 100%;

	overflow: hidden;
}

.a-topic .b .item:last-child {
	// margin-right: 30rpx;
}

.a-topic .b .img {
	height: 387.5rpx;
	width: 680.5rpx;
	margin-bottom: 30rpx;
}

.a-topic .b .np {
	height: 55rpx;
	text-align: center;
	width: 680.5rpx;
}

.a-topic .b .np .price {
	padding-left: 40rpx;
}

.a-topic .b .desc {
	display: block;
	height: 39rpx;
	text-align: center;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.jhx_f8 {
	font-size: 33rpx;
	color: #ec4223;
	font-weight: bold;
}

.jhx_f4 {
	font-size: 30rpx;
	color: #333;
	font-weight: bold;
}
</style>
